<template>
  <table class="center" border="0" cellspacing="0" cellpadding="0">
    <tbody>
      <tr>
        <td></td>
        <th v-for="(player, index) in tournament.players" :key="index" :title="player.name">
          {{ player.name }}
        </th>
      </tr>

      <tr v-for="(player, y) in tournament.players" :key="y">
        <td class="bold">{{ player.name }}</td>
        <td v-for="(opponent, x) in tournament.players" :key="x">
          <span
            v-if="matrixLookup[y][x] == null || matrixLookup[y][x] == undefined"
            >_</span
          >
          <span v-else-if="matrixLookup[y][x].matchId === -1">X</span>
          <div v-else-if="matrixLookup[y][x].matchId > -1">
            <div
              v-if="
                getMatch(y, x).gameId == 0 &&
                  getMatch(y, x).gameFinished == false
              "
            >
              ...
            </div>
            <div v-else-if="getMatch(y, x).gameFinished == true">
              <div v-if="getMatch(y, x).winner == null">DRAW</div>
              <div
                v-else-if="getMatch(y, x).winner.name == player.name"
                class="win"
              >
                WIN
              </div>
              <div v-else class="lose">
                LOSE
              </div>
            </div>
            <button
              class="emoji"
              v-else-if="getMatch(y, x).gameFinished == false"
              v-on:click="showGame(getMatch(y, x).gameId)"
            >
              🎲
            </button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Tournament, Match } from "@/types";

interface LookupInfo {
  matchId: number;
  round: number;
  nrInRound: number;
}
export default defineComponent({
  name: "TournamentMatrixView",
  props: {
    tournament: {
      type: Object as () => Tournament,
      required: true
    }
  },
  data() {
    return {
      matrixLookup: [[]] as LookupInfo[][],
      id2matchLookup: [] as number[][]
    };
  },

  methods: {
    showGame(id: number) {
      this.$router.push({ name: "Game", params: { gameId: id } });
    },

    getMatch(y: number, x: number): Match {
      return this.tournament.rounds[this.matrixLookup[y][x].round].matches[
        this.matrixLookup[y][x].nrInRound
      ];
    },

    createLookupTable() {
      if (this.tournament == null || this.tournament == undefined) return;

      const nrPlayers = this.tournament.players.length;
      this.matrixLookup = new Array(nrPlayers);
      for (let i = 0; i < nrPlayers; i++)
        this.matrixLookup[i] = new Array(nrPlayers);

      let matchId = 1; // TODO: This shouldbe based on the game type
      for (let y = 0; y < nrPlayers; y++) {
        for (let x = 0; x < nrPlayers; x++) {
          if (y == x) {
            this.matrixLookup[y][x] = { matchId: -1, round: -1, nrInRound: -1 };
          }
          if (y < x) {
            this.matrixLookup[y][x] = {
              matchId: matchId,
              round: 0,
              nrInRound: matchId - 1
            };
            matchId++;
          }
          if (y > x) {
            this.matrixLookup[y][x] = this.matrixLookup[x][y];
          }
        }
      }
    }
  },

  created() {
    this.createLookupTable();
  }
});
</script>

