table {
  border-collapse: collapse;

  tr:first-child:hover {
    background-color: inherit;
  }
  tr:hover {
    background-color: #6e91a330;
  }

  th {
    max-width: 100px;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

.bold {
  font-weight: bold;
}
</style>
