
import { defineComponent } from "vue";
import { Tournament, Match } from "@/types";

interface LookupInfo {
  matchId: number;
  round: number;
  nrInRound: number;
}
export default defineComponent({
  name: "TournamentMatrixView",
  props: {
    tournament: {
      type: Object as () => Tournament,
      required: true
    }
  },
  data() {
    return {
      matrixLookup: [[]] as LookupInfo[][],
      id2matchLookup: [] as number[][]
    };
  },

  methods: {
    showGame(id: number) {
      this.$router.push({ name: "Game", params: { gameId: id } });
    },

    getMatch(y: number, x: number): Match {
      return this.tournament.rounds[this.matrixLookup[y][x].round].matches[
        this.matrixLookup[y][x].nrInRound
      ];
    },

    createLookupTable() {
      if (this.tournament == null || this.tournament == undefined) return;

      const nrPlayers = this.tournament.players.length;
      this.matrixLookup = new Array(nrPlayers);
      for (let i = 0; i < nrPlayers; i++)
        this.matrixLookup[i] = new Array(nrPlayers);

      let matchId = 1; // TODO: This shouldbe based on the game type
      for (let y = 0; y < nrPlayers; y++) {
        for (let x = 0; x < nrPlayers; x++) {
          if (y == x) {
            this.matrixLookup[y][x] = { matchId: -1, round: -1, nrInRound: -1 };
          }
          if (y < x) {
            this.matrixLookup[y][x] = {
              matchId: matchId,
              round: 0,
              nrInRound: matchId - 1
            };
            matchId++;
          }
          if (y > x) {
            this.matrixLookup[y][x] = this.matrixLookup[x][y];
          }
        }
      }
    }
  },

  created() {
    this.createLookupTable();
  }
});
