    <h2>Finished games:</h2>
    <ShowMatches :onlyFinishedGames="true" :ofTournament="true" />
  </template>
  <p v-else>
    Sorry, the server is offline at the moment. Please try again later.
  </p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Tournament from "@/components/Tournament.vue";
import store from "@/store";
import ShowMatches from "@/components/ShowMatches.vue";

export default defineComponent({
  name: "Home",
  components: {
    ShowMatches,
    Tournament
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    }
  },
  mounted() {
    document.title = "Game Server | Tournament";
  }
});
