
import { defineComponent } from "vue";
import { Tournament, Player, Match } from "@/types";

export default defineComponent({
  name: "TournamentRoundsView",
  props: {
    tournament: {
      type: Object as () => Tournament,
      required: true
    }
  },
  methods: {
    showGame(id: number) {
      this.$router.push({ name: "Game", params: { gameId: id } });
    },
    getWinOrLose(match: Match, me: Player) {
      if (match.winner == null) {
        return "";
      }
      if (match.winner.name == me.name) {
        return "win";
      }

      return "lose";
    }
  }
});
