<template>
  <div class="wrapper" v-if="game != null" :data-title="game.endReason">
    <table class="center" cellspacing="0" cellpadding="0" border="0">
      <tr>
        <td>{{ game.scores[0] }}</td>
        <td>
          <img
            class="playericon"
            :src="require('../assets/' + game.tileImages[1])"
            v-if="game.tileImages[1] !== ''"
            alt="white"
          />
          <span
            :class="getWinOrLose(game, game.playerWhite)"
            v-if="game.playerWhite != null"
          >
            {{ game.playerWhite.name }}
          </span>
        </td>
        <td>vs</td>
        <td>
          <img
            class="playericon"
            :src="require('../assets/' + game.tileImages[2])"
            v-if="game.tileImages[2] !== ''"
            alt="black"
          />
          <span
            :class="getWinOrLose(game, game.playerBlack)"
            v-if="game.playerBlack != null"
          >
            {{ game.playerBlack.name }}
          </span>
        </td>
        <td>{{ game.scores[1] }}</td>
      </tr>
      <tr>
        <td colspan="5">&nbsp;{{ game.endReason }}&nbsp;</td>
      </tr>
    </table>

    <div class="board" v-bind:class="game.gameType">
      <div class="row" v-for="(line, index) in game.board.board" :key="index">
        <div class="cell" v-for="(cell, index) in line" :key="index">
          <img
            class="img"
            :src="require('../assets/' + game.tileImages[cell])"
            v-if="game.tileImages[cell] !== ''"
            alt="{{ cell }}"
          />
          <img
            class="img"
            src="data:image/svg+xml;utf8,&lt;svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1 1'&gt;&lt;/svg&gt;"
            v-if="game.tileImages[cell] === ''"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Game, BoardUpdate, OnResultData, Player } from "@/types";

export default defineComponent({
  name: "ShowGame",
  props: {
    gameId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      comment: "" as string,
      game: (null as unknown) as Game
    };
  },
  methods: {
    subscribeForUpdates() {
      // receive updates about current game
      this.$websocket.subscribe("/topic/gameupdate/" + this.gameId, update => {
        const data: BoardUpdate = JSON.parse(update.body);
        this.game.board.board[Math.floor(data.index / this.game.board.width)][
          data.index % this.game.board.width
        ] = data.value;
        this.game.scores = data.scores;
      });
    },
    subscribeForResult() {
      this.$websocket.subscribe("/topic/gameresult/" + this.gameId, update => {
        const data: OnResultData = JSON.parse(update.body);
        this.game.winner = data.winner.name;
        this.game.endReason = data.comment;
      });
    },
    getWinOrLose(game: Game, me: Player) {
      if (game.winner == null) {
        return "";
      }
      if (game.winner == me.name) {
        return "win";
      }

      return "lose";
    }
  },
  mounted() {
    this.$http
      .get("/game/" + this.gameId)
      .then(res => {
        this.game = res.data;
        // subsribe after setting this.game
        this.subscribeForUpdates();
        this.subscribeForResult();
      })
      .catch(console.error);
  }
});
</script>

