<template>
  <div
    class="round"
    v-for="(round, roundId) in tournament.rounds"
    :key="roundId"
  >
    <h3 v-if="tournament.rounds.length > 1">Round: {{ roundId + 1 }}</h3>
    <table class="mt1 center" border="0" cellspacing="0" cellpadding="0">
      <tr v-for="(match, matchId) in round.matches" :key="matchId">
        <td>
          <span
            :class="getWinOrLose(match, match.playerWhite)"
            v-if="match.playerWhite != null"
            :title="match.playerWhite.name"
          >
            {{ match.playerWhite.name }}</span
          >
        </td>
        <td>VS</td>
        <td>
          <span
            :class="getWinOrLose(match, match.playerBlack)"
            v-if="match.playerBlack != null"
            :title="match.playerBlack.name"
          >
            {{ match.playerBlack.name }}</span
          >
        </td>
        <td>
          <button
            class="emoji"
            v-if="match.gameId != 0 && !match.gameFinished"
            v-on:click="showGame(match.gameId)"
          >
            🎲
          </button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Tournament, Player, Match } from "@/types";

export default defineComponent({
  name: "TournamentRoundsView",
  props: {
    tournament: {
      type: Object as () => Tournament,
      required: true
    }
  },
  methods: {
    showGame(id: number) {
      this.$router.push({ name: "Game", params: { gameId: id } });
    },
    getWinOrLose(match: Match, me: Player) {
      if (match.winner == null) {
        return "";
      }
      if (match.winner.name == me.name) {
        return "win";
      }

      return "lose";
    }
  }
});
</script>
