
import { defineComponent } from "vue";
import { Tournament, Match, OnTournamentFinished } from "@/types";
import TournamentRankings from "@/components/TournamentRankings.vue";
import TournamentTreeView from "@/components/TournamentTreeView.vue";
import TournamentMatrixView from "@/components/TournamentMatrixView.vue";
import TournamentRoundsView from "@/components/TournamentRoundsView.vue";
import ShowMatches from "@/components/ShowMatches.vue";
import store from "@/store";

export default defineComponent({
  name: "Tournament",
  components: {
    TournamentRankings,
    TournamentTreeView,
    TournamentMatrixView,
    TournamentRoundsView,
    ShowMatches
  },
  data() {
    return {
      tournaments: [] as Tournament[]
    };
  },
  methods: {
    websocketUpdates() {
      this.$websocket.subscribe("/topic/onnewtournament", update => {
        // HTTP request to get full data of new tournament
        this.getTournament();

        // Reset MatrixView
        if (this.$refs.matrixView != null) {
          (this.$refs
            .matrixView as typeof TournamentMatrixView).createLookupTable();
        }
      });

      this.$websocket.subscribe("/topic/ontournamentfinished", update => {
        const onFinished: OnTournamentFinished = JSON.parse(update.body);
        let allFinished = true;
        for (let i = 0; i < this.tournaments.length; i++) {
          if (this.tournaments[i].tournamentId == onFinished.tournamentId) {
            this.tournaments[i].rankings = onFinished.rankings;
          }
          allFinished = allFinished && this.tournaments[i].rankings != null;
        }
        if (allFinished) {
          store.state.warning = "Tournament has ended!";
          store.state.fireworks = true;
          setTimeout(() => (store.state.fireworks = false), 10000);
        }
      });

      // receive updates about matches
      this.$websocket.subscribe("/topic/onmatchupdate", update => {
        const match: Match = JSON.parse(update.body);

        for (let i = 0; i < this.tournaments.length; i++) {
          if (this.tournaments[i].tournamentId != match.tournamentId) {
            continue;
          }

          const tour = this.tournaments[i];
          for (let roundNr = 0; roundNr < tour.rounds.length; roundNr++) {
            const round = tour.rounds[roundNr];
            for (let matchNr = 0; matchNr < round.matches.length; matchNr++) {
              if (round.matches[matchNr].matchId === match.matchId) {
                this.tournaments[i].rounds[roundNr].matches[matchNr] = match;
                return;
              }
            }
          }
        }
      });
    },
    getTournament() {
      this.$http
        .get("/tournament")
        .then(res => {
          if (res.data) {
            this.tournaments = res.data;
            for (let i = 0; i < this.tournaments.length; i++) {
              this.tournaments[i].view =
                this.tournaments[i].tournamentType == "roundrobin" ? 1 : 4;
            }
          }
        })
        .catch(console.error);
    }
  },
  mounted() {
    this.getTournament();
    this.websocketUpdates();
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onnewtournament");
    this.$websocket.unsubscribe("/topic/ontournamentfinished");
    this.$websocket.unsubscribe("/topic/onmatchupdate");
  }
});
