
import { defineComponent } from "vue";
import ShowGame from "@/components/ShowGame.vue";
import { Game } from "@/types";

export default defineComponent({
  name: "Games",
  components: {
    ShowGame
  },
  data() {
    return {
      games: [] as Game[]
    };
  },
  props: {
    tournamentId: {
      type: Number,
      required: false,
      default: 0
    },
    ofTournament: {
      type: Boolean,
      required: false,
      default: false
    },
    latest: {
      type: Boolean,
      default: false
    },
    onlyFinishedGames: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    // important: subscribe first before http request

    // receive updates about new games
    this.$websocket.subscribe("/topic/onnewgame", update => {
      const game: Game = JSON.parse(update.body);
      if (this.tournamentId != 0 && game.tournamentId != this.tournamentId) {
        return;
      }

      if (this.ofTournament && game.tournamentId == null) {
        return;
      }

      this.games.unshift(game);

      if (this.latest) {
        this.games = this.games.slice(0, 1);
      } else {
        this.games = this.games.slice(0, 100); // Show a maximum of 100 games, so we don't eat people's RAM.
      }
    });

    // receive updates about finished games
    this.$websocket.subscribe("/topic/ongameended", update => {
      const gameId: number = JSON.parse(update.body);
      const game = this.games.find(g => g.gameId == gameId);
      if (game !== undefined) {
        game.gameEnded = true;
      }
    });

    // get the full games list once using a HTTP request:
    this.$http
      .get("/gamelist")
      .then(res => {
        res.data = res.data.reverse();

        res.data.forEach((game: Game) => {
          if (this.latest && this.games.length > 1) {
            return;
          }
          if (this.games.length >= 100) {
            return;
          }

          if (!(this.ofTournament && game.tournamentId == null)) {
            if (
              this.tournamentId == 0 ||
              game.tournamentId == this.tournamentId
            ) {
              this.games.push(game);
            }
          }
        });
      })
      .catch(console.error);
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onnewgame");
    this.$websocket.unsubscribe("/topic/ongameended");
  }
});
